import IconLabel from "./icon-label.vue";
export default {
  computed: {
    isSchoolRoute: function isSchoolRoute() {
      return this.$store.getters["authenticationStore/isSchoolRoute"];
    },
    selectedSchool: function selectedSchool() {
      console.log("selectedSchool: ", this.$store.getters["schoolStore/selectedSchool"]);
      return this.$store.getters["schoolStore/selectedSchool"];
    },
    email: function email() {
      return this.$store.getters["ContactStore/officialEmail"];
    },
    phoneNumber: function phoneNumber() {
      return this.$store.getters["ContactStore/officialPhoneNumber1"];
    }
  },
  components: {
    IconLabel: IconLabel
  },
  props: {
    phoneIcon: {
      type: String,
      default: "phone"
    },
    emailIcon: {
      type: String,
      default: "email"
    },
    facebookIcon: {
      type: String,
      default: "facebook"
    },
    twitterIcon: {
      type: String,
      default: "twitter"
    },
    linkedInIcon: {
      type: String,
      default: "linkedin"
    }
  },
  methods: {
    create: function create() {
      var routename = this.$store.getters["authenticationStore/createURL"];
      this.$router.push(routename);
    }
  }
};